//Desktop
.chart-container {
    @media only screen and (max-width: 959px) {
        display: none;
    }
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #474747;

    .chart-wrapper,
    .chart-wrapper-expand {
        width: 48%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        .chart-header {
            margin: 5px 0 0 0;
            height: 6%;
            font-size: clamp(9px, 1vw, 13px);
            @media only screen and (max-height: 700px) {
                font-size: clamp(9px, 0.9vw, 12px);
            }
            @media only screen and (max-height: 600px) {
                font-size: clamp(9px, 0.8vw, 11px);
            }
            @media only screen and (max-height: 550px) {
                font-size: clamp(8px, 0.7vw, 10px);
            }
            @media only screen and (max-height: 500px) {
                font-size: clamp(8px, 0.6vw, 9px);
            }
            @media only screen and (max-height: 450px) {
                font-size: clamp(7px, 0.5vw, 8px);
            }
        }

        .chart {
            width: 100%;
            height: 82%;
        }
    }

    //expand
    .chart-wrapper-expand {
        .chart-header {
            height: 3%;
            margin: 5px 0 0 0;
            font-size: clamp(12px, 1vw, 16px);
            @media only screen and (max-height: 700px) {
                font-size: clamp(11px, 1.1vw, 15px);
            }
            @media only screen and (max-height: 600px) {
                font-size: clamp(10px, 1.1vw, 14px);
            }
            @media only screen and (max-height: 550px) {
                font-size: clamp(9px, 1.1vw, 13px);
            }
            @media only screen and (max-height: 500px) {
                font-size: clamp(9px, 1.1vw, 12px);
            }
            @media only screen and (max-height: 450px) {
                font-size: clamp(8px, 1.1vw, 11px);
            }
        }

        .chart {
            width: 100%;
            height: 90%;
        }
    }

    .arrow-toggler {
        background-color: transparent;
        border: none;
        margin-bottom: 5px;
        height: 5%;
    }

    .border {
        align-self: flex-end;
        width: 1px;
        height: 100%;
        background-color: #555;
    }
}

//button toggler containter for expand
.button-toggler-container-expand {
    display: flex;
}

//Tablet and Mobile
.chart-container-tablet,
.chart-container-tablet-version,
.chart-container-mobile,
.chart-container-mobile-version{
    display: flex;
    flex-direction:column;
    align-items: center;
    justify-content: space-evenly;
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #474747;
    @media only screen and (min-width: 959px) {
        flex-direction:row;
    }

    .chart-wrapper {
        width: 98%;
        height: 48%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        @media only screen and (min-width: 959px) {
            width: 48%;
            height: 98%;
        }
        .chart-header {
            margin: 0;
            font-size: clamp(9px, 1.4vw, 14px);
            @media only screen and (max-height: 550px) {
                font-size: clamp(9px, 1.4vw, 13px);
            }
            @media only screen and (max-height: 500px) {
                font-size: clamp(8px, 1.4vw, 12px);
            }
            @media only screen and (max-height: 450px) {
                font-size: clamp(8px, 1.4vw, 11px);
            }
        }

        .chart {
            width: 98%;
            height: 90%;

            @media only screen and (min-width: 959px) {
                width: 98%;
                height: 95%;
            }
        }
    }

    .border {
        width: 100%;
        height: 1px;
        background-color: #555;
        @media only screen and (min-width: 959px) {
            width: 1px;
            height: 100%;
        }
    }
}

.button-toggler-container-tablet,
.button-toggler-container-tablet-version {
    display: flex;
    align-self: flex-start;
}

.chart-container-tablet{
    @media only screen and (min-width: 959px) {
        display: none;
    }
    @media only screen and (max-width: 500px) {
        display: none;
    }
}

//Mobile
.chart-container-mobile{
    @media only screen and (min-width: 501px) {
        display: none;
    }
}



//loader
.loader-country-chart-wrapper {
    width: 100%;
    height: 100%;
    background-color: #474747;
}
.loader-country-chart-wrapperexpand {
    width: 100%;
    height: 96.3%;
    background-color: #474747;
}



.chart-container-hide,
.button-toggler-container-hide {
    display: none;
}
